/* Theme vars (unchanged) */
:root{
  --brand:#6d28d9;
  --brand-700:#5b21b6;
  --accent:#15803d;
  --accent-700:#166534;
  --fs:100%;
}

html{ scroll-behavior:smooth; font-size:var(--fs); }
html,body{ height:100%; }
body {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(30 41 59 / var(--tw-text-opacity));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img, video, canvas, svg { max-width:100%; height:auto; }
.container{ max-width:1280px; margin-inline:auto; padding-inline:1rem; }

.full-bleed{
  position:relative;
  margin-left:calc(50% - 50vw);
  margin-right:calc(50% - 50vw);
  inline-size:100svw; width:100vw;
  min-height:60vh;
}
@media (min-width:768px){ .full-bleed{ min-height:70vh; } }

.object-cover{ object-fit:cover; }
.will-change-transform{ will-change:transform; }

.section-lazy{
  content-visibility:auto;
  contain-intrinsic-size: 1px 800px;
}
@supports not (content-visibility:auto){ .section-lazy{} }

.nav-glass {
  position: sticky;
  top: 0px;
  z-index: 40;
  backdrop-filter:blur(8px);
  background: rgba(255,255,255,.75);
  background: color-mix(in oklab, white 70%, transparent);
  border-bottom:1px solid rgba(0,0,0,.06);
}

/* Buttons (keep high contrast) */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  text-shadow:0 1px 0 rgba(0,0,0,.25);
  /* ✅ A11Y: WCAG dokunma hedefi */
  min-height: 44px;
  min-width: 44px;
}
.btn-primary{ background:var(--brand); }
.btn-primary:hover{ background:var(--brand-700); }
.btn-accent{ background:var(--accent); }
.btn-accent:hover{ background:var(--accent-700); }

/* REVERT badges to glassy look */
.badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  border-radius: 9999px;
  border-width: 1px;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  --tw-backdrop-blur: blur(8px);
  backdrop-filter: var(--tw-backdrop-blur) var(--tw-backdrop-brightness) var(--tw-backdrop-contrast) var(--tw-backdrop-grayscale) var(--tw-backdrop-hue-rotate) var(--tw-backdrop-invert) var(--tw-backdrop-opacity) var(--tw-backdrop-saturate) var(--tw-backdrop-sepia);
  background:rgba(255,255,255,.10);
  color:#fff;
  border-color:rgba(255,255,255,.20);
  /* ✅ A11Y: Minimum dokunma hedefi */
  min-height: 40px;
}

.card {
  border-radius: 1rem;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding: 1.5rem;
  --tw-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 1px 3px 0 var(--tw-shadow-color), 0 1px 2px -1px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.card:hover {
  --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.faq-card {
  border-radius: 0.75rem;
  border-width: 1px;
  background:rgba(255,255,255,.95);
  border-color:rgba(0,0,0,.08);
}

.hero-overlay{
  background:linear-gradient(180deg, rgba(0,0,0,.6), rgba(0,0,0,.45) 35%, rgba(0,0,0,.6));
}

/* Keep a clear focus ring (subtle but visible) */
:focus-visible{ 
  outline:2px solid #6d28d966; 
  outline-offset:2px;
  /* ✅ A11Y: Daha belirgin focus */
  outline-width: 3px;
}

/* ✅ A11Y: Skip to main content link */
.skip-to-main {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #6d28d9;
  color: white;
  padding: 8px 12px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 10000;
  font-weight: 600;
}
.skip-to-main:focus {
  top: 6px;
}

html.hc body{ 
  filter:contrast(1.06) saturate(1.05); 
  /* ✅ A11Y: Daha güçlü kontrast */
  filter: contrast(1.1) saturate(1.1);
}
html.hc a{ 
  text-decoration:underline;
  /* ✅ A11Y: Daha kalın underline */
  text-decoration-thickness: 2px;
}

@supports (padding:max(0px)){
  .pb-safe{ padding-bottom:max(1rem, env(safe-area-inset-bottom)); }
}

/* Burst effect (unchanged) */
.burst-particle{
  position:absolute; left:0; top:0;
  background:radial-gradient(circle, var(--burst-c1,#6d28d9), var(--burst-c2,#22c55e));
  border-radius:9999px;
  transform:translate(-50%,-50%);
  animation:burst-move var(--life,550ms) ease-out forwards, burst-fade var(--life,550ms) ease-out forwards;
  pointer-events:none;
  will-change:transform,opacity;
  box-shadow:0 0 10px rgba(0,0,0,.12);
}
@keyframes burst-move{
  from{ transform:translate(-50%,-50%) rotate(0) scale(1); }
  to{ transform:translate(calc(-50% + var(--dx)), calc(-50% + var(--dy))) rotate(var(--dr)) scale(.6); }
}
@keyframes burst-fade{
  0%{ opacity:1; }
  80%{ opacity:.7; }
  100%{ opacity:0; }
}
@media (prefers-reduced-motion: reduce){
  .burst-particle{ 
    animation: burst-fade 400ms ease-out forwards; 
    transform: translate(-50%,-50%) scale(.8);
    /* ✅ A11Y: Daha hızlı kapanma */
    animation-duration: 200ms;
  }
}

.bar-item{
  display:grid; place-items:center; height:3rem; width:100%;
  font-size:.875rem; font-weight:600; color:rgb(38 38 38);
  border-radius:.5rem; position: relative;
  /* ✅ A11Y: Garantili dokunma hedefi */
  min-height: 48px;
}
.bar-item:hover{ background:#f8fafc; }
.bar-item:focus-visible{ 
  outline:2px solid #6d28d966; 
  outline-offset:2px;
  /* ✅ A11Y: Daha belirgin focus */
  outline-width: 3px;
}

.img-skeleton{ 
  background:#f3f4f6; 
  /* ✅ A11Y: Yüksek kontrast fallback */
  color: #374151;
}

@supports (font-synthesis-weight: auto){
  html{ font-synthesis-weight: auto; font-synthesis-style: auto; }
}

/* ---- Minimal A11Y-only nudges ---- */
/* 1) Ensure hero small text is not translucent if class missed */
.hero-para { 
  color:#ffffff; 
  /* ✅ A11Y: Minimum kontrast garantisi */
  opacity: 0.95;
}
/* 2) Avoid low-contrast grays on small text blocks */
.text-muted-safe { 
  color:#475569; /* slate-600 */
  /* ✅ A11Y: Daha güvenli font boyutu */
  font-size: 0.875rem;
  line-height: 1.5;
}

/* ✅ A11Y: Küçük fontlar için ek kontrast güvenliği */
.text-xs-safe { 
  font-size: 0.75rem;
  line-height: 1.25;
  color: #475569;
}

/* ✅ A11Y: Form elementleri için focus improvement */
input:focus-visible,
textarea:focus-visible,
select:focus-visible {
  outline: 2px solid #6d28d9;
  outline-offset: 1px;
  border-color: #6d28d9;
}

/* ✅ A11Y: Görsel alt text backup için daha iyi kontrast */
.img-fallback {
  background: #f8fafc;
  color: #374151;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}

/* === FAQ modern stil (WCAG-safe) === */

.faqGlass{
  /* mor+yeşil hafif vinyet + cam efekti */
  background:
    radial-gradient(1200px 600px at 10% -10%, rgba(99,102,241,.10), transparent),
    radial-gradient(900px 500px at 110% 10%, rgba(16,185,129,.10), transparent),
    rgba(255,255,255,.72);
  backdrop-filter: saturate(120%) blur(6px);
  border:1px solid rgba(0,0,0,.06);
  border-radius: 1rem; /* 16px */
}

/* küçük kategori/etiket butonları (opsiyonel) */
.faqChip{
  border:1px solid rgba(0,0,0,.08);
  background:#fff;
  color:#0f172a; /* slate-900 */
  border-radius: 9999px;
  padding:.375rem .75rem; /* 6px 12px */
  font-weight:600;
  transition: background .2s ease, transform .15s ease, box-shadow .2s ease;
  /* ✅ A11Y: Minimum dokunma hedefi */
  min-height: 32px;
  display: inline-flex;
  align-items: center;
}
.faqChip:hover{ background:#f8fafc; transform:translateY(-1px); }
.faqChip:focus-visible{
  outline:2px solid #6d28d9; 
  outline-offset:2px;
  /* ✅ A11Y: Daha belirgin focus */
  outline-width: 3px;
}

/* her bir <details> kartı */
.faqCard{
  border:1px solid rgba(0,0,0,.08);
  background:#fff;
  border-radius:.75rem; /* 12px */
  transition: border-color .2s ease, box-shadow .2s ease, transform .15s ease;
}
.faqCard[open]{
  border-color:#6366f1;           /* indigo-500 */
  box-shadow:0 10px 30px rgba(2,6,23,.08);
  transform:translateY(-1px);
}

/* summary trigger'ı için net focus halkası */
.faqSummary{
  display:flex; align-items:center; justify-content:space-between; gap:.75rem;
  font-weight:700; color:#0f172a;  /* slate-900 */
  border-radius:.5rem;
  /* ✅ A11Y: Minimum dokunma hedefi */
  min-height: 48px;
  padding: 0 1rem;
}
.faqSummary:focus-visible{
  outline:2px solid #6d28d9;             /* brand moru */
  outline-offset:2px;
  /* ✅ A11Y: Daha belirgin focus */
  outline-width: 3px;
}

/* içerik görünür olurken animasyon */
.faqAnim{ animation:faqFade .25s ease both; }
@keyframes faqFade{
  from{ opacity:0; transform:translateY(-4px); }
  to  { opacity:1; transform:translateY(0); }
}

/* hareketi azalt tercihine saygı */
@media (prefers-reduced-motion: reduce){
  .faqCard[open]{ transform:none; }
  .faqChip:hover{ transform:none; }
  .faqAnim{ animation:none; }
  /* ✅ A11Y: Tüm transition'ları kapat */
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
  }
}

/* ✅ A11Y: High contrast media query desteği */
@media (prefers-contrast: high) {
  .btn {
    border: 2px solid currentColor;
  }
  
  .card {
    border: 2px solid #374151;
  }
  
  .faqCard {
    border: 2px solid #374151;
  }
}

/* ✅ A11Y: Print stilleri */
@media print {
  .nav-glass,
  .bar-item,
  .burst-particle {
    display: none !important;
  }
  
  .card {
    box-shadow: none;
    border: 1px solid #000;
  }
  
  a::after {
    content: " (" attr(href) ")";
    font-size: 0.875rem;
    color: #666;
  }
}

/* ✅ A11Y: Ek güvenlik stilleri */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
